






























































var pageOptions,shop;

export default {
	data() {
		return {
			oauthUserInfo: {}
		};
	},
	onLoad: function(params) {
		pageOptions = params;
		if (pageOptions.backpage) {
			pageOptions.backpage = decodeURIComponent(pageOptions.backpage);
		}
		// console.log(pageOptions);
		//console.log(params);
		//this.wxLogin();
	},
	onShow() {
	//shop = uni.getStorageSync('locationShop');
	//uni.showToast({
	//title:'店铺id'+shop.id
	//})
		// console.log(uni.getStorageSync('userLoginInfo'));
		// console.log(uni.getStorageSync('locationShop'));
		// console.log(uni.getStorageSync('referee'));
		// console.log(uni.getStorageSync('launchFlag'));
	},
	methods: {
		loginSubmit: function(e) {
			uni.showLoading({
				title: '登录中...',
				mask: false
			});
			uni.request({
				url: this.apiUrl + 'login/login',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					phone: e.detail.value.phone,
					password: e.detail.value.password
				},
				success: res => {
					uni.hideLoading();
					if (res.data.code == 0) {
						uni.setStorageSync('userLoginInfo', res.data.data);
						uni.setStorageSync('locationShop', res.data.data.shop);

						if (!res.data.data.openid) {
							this.bindMpOpenid(res.data.data.phone);
						}

						uni.showToast({
							title: '登录成功',
							icon: 'none'
						});
						if (pageOptions.backpage === undefined) {
							uni.reLaunch({
								url: '../mine/mine'
							});
						}
						if (pageOptions.backtype == 1) {
							uni.reLaunch({
								url: pageOptions.backpage
							});
						} else {
							uni.reLaunch({
								url: pageOptions.backpage
							});
						}

					} else {
						uni.showToast({
							title: '用户名密码错误',
							icon: 'none'
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
			//console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		},
		bindMpOpenid: function(mobile) {
			uni.login({
				success: res => {
					uni.request({
						url: this.apiUrl + 'login/bindMpOpenid',
						method: 'GET',
						data: {
							code: res.code,
							phone: mobile
						},
						success: res => {
							if (res.data.code == 0) {
								return res.data.data.openid;
							}
						},
						fail: () => {},
						complete: () => {}
					});
				}
			});
		},
		// 第三方微信小程序  微信登录

		getuserinfo(e) {
			let shop = uni.getStorageSync('locationShop');
			uni.getUserInfo({
				provider: 'weixin',
				success: infoRes => {
					uni.login({
						success: loginRes => {
							// console.log(shop_id);
							uni.request({
								url: this.apiUrl + 'login/getMpToken',
								method: 'GET',
								data: {
									code: loginRes.code,
									shop_id: shop.id,
									mpUserInfo: e.detail.userInfo
								},
								success: res => {
									uni.setStorageSync('userLoginInfo', res.data.data);
									uni.setStorageSync('locationShop', res.data.data.shop);
									uni.showToast({
										title: '登录成功',
										icon: 'none'
									});
									if (pageOptions.backpage === undefined) {
										uni.reLaunch({
											url: '../mine/mine'
										});
									}
									if (pageOptions.backtype == 1) {
										uni.reLaunch({
											url: pageOptions.backpage
										});
									} else {
										uni.reLaunch({
											url: pageOptions.backpage
										});
									}
								},
								fail: () => {},
								complete: () => {}
							});
						}
					});
				}
			});
		},

		// 第三方App 微信登录
		getuserappinfo() {
			let shop = uni.getStorageSync('locationShop');
			let referee = uni.getStorageSync('referee');
			
			uni.login({
				provider: 'weixin',
				success: loginRes => {
					uni.request({
						url: this.apiUrl + 'login/getAppWechatToken',
						method: 'GET',
						data: {
							openid: loginRes.authResult.openid,
							shop_id: shop.id,
							access_token: loginRes.authResult.access_token,
							unionid: loginRes.authResult.unionid,
							referee: referee
						},
						success: res => {
							//console.log(JSON.stringify(res));
							if (res.data.code == 0) {
								uni.setStorageSync('userLoginInfo', res.data.data);
								uni.setStorageSync('locationShop', res.data.data.shop);
								uni.showToast({
									title: '登录成功',
									icon: 'none'
								});
								if (pageOptions.backpage === undefined) {
									uni.reLaunch({
										url: '../mine/mine'
									});
								}
								if (pageOptions.backtype == 1) {
									uni.reLaunch({
										url: pageOptions.backpage
									});
								} else {
									uni.reLaunch({
										url: pageOptions.backpage
									});
								}
							} else {
								uni.showToast({
									title: '登陆失败',
									icon: 'none'
								});
							}
						},
						fail: () => {},
						complete: () => {}
					});
				}
			});
		},
		// 第三方登录
		loginThree: function() {}
	}
};
